import React from 'react';
import "./index.css";

const Select = ({id,name,options})=> {

	const opts = options.map(
		elem=> 
		<option
		className='select select-junior'
		value={elem}
		>{elem}
		</option>

	);

	return (
		<select 
		className='select select-junior'
		name={name}>
			<option
				className='select select-junior'
			 	value=""
			 ></option>
			{opts}
		</select>

	)

}

export default Select;